.nav {
  display: grid;
  list-style: none;
  margin: 0;
  padding: 5px 0;
  grid-template-columns: 1fr;
  justify-items: center;
  // background: $orange;
  h2 {
    color: $white;
    padding: 0;
    margin: 0;
    font-size: 50px;
    line-height: 1;
    text-transform: none;
  }
}


